import React, { useState, useEffect } from "react";
import { Button, Select } from "antd";
import { Link } from "react-router-dom";
import AdSense from "react-adsense";

const { Option } = Select;

const children = [];
const mbtiArr = [
  "ESTJ",
  "ESTP",
  "ESFJ",
  "ESFP",
  "ENTJ",
  "ENTP",
  "ENFJ",
  "ENFP",
  "ISTJ",
  "ISTP",
  "ISFJ",
  "ISFP",
  "INTJ",
  "INTP",
  "INFJ",
  "INFP",
];
for (let i = 0; i < 16; i++) {
  children.push(<Option key={mbtiArr[i]}>{mbtiArr[i]}</Option>);
}
export const Home = () => {
  const [isLoading, setIsLoading] = useState(false);
  const handleChange = (value) => {
    localStorage.setItem("my-mbti", value);
  };

  useEffect(() => {
    setTimeout(() => {
      setIsLoading(true);
    }, 1000);
    localStorage.setItem("my-mbti", "ESTJ");
  }, []);
  return (
    <div className="flex justify-center flex-col">
      <Link to="/compatibility">
        <div className="text-right pt-2 pr-3 font-default">MBTI 궁합보기</div>
      </Link>
      <h1 className="text-3xl text-center pt-16">
        MBTI 검사 성격 이상형 테스트
      </h1>
      <p className="text-center mb-2">
        내가 좋아하는 상대방의 MBTI는 무엇일까?
      </p>
      <p className="text-center mb-2">본인의 MBTI 유형을 선택하여 주세요.</p>
      <div className="text-center mb-2">
        <Select
          size="large"
          defaultValue="ESTJ"
          onChange={handleChange}
          style={{ width: 336 }}
        >
          {children}
        </Select>
      </div>
      <AdSense.Google
        style={{ display: "block" }}
        client="ca-pub-9130836798889522"
        slot="7740004665"
        format="auto"
        responsive="true"
      />
      <div className="text-center mt-2">
        {isLoading ? (
          <Button style={{ width: 336 }} type="primary" size="large">
            <Link to="/page1">START</Link>
          </Button>
        ) : (
          ""
        )}
      </div>
    </div>
  );
};
