import React from "react";
import "./App.css";
import {
  Home,
  Page1,
  Page2,
  Page3,
  Page4,
  Complete,
  Compatibility,
  Result,
} from "components";
import { HashRouter as Router, Route } from "react-router-dom";

const App = () => {
  return (
    <div className="App">
      <Router>
        <Route path="/" exact={true} component={Home} />
        <Route path="/page1" exact={true} component={Page1} />
        <Route path="/page2" exact={true} component={Page2} />
        <Route path="/page3" exact={true} component={Page3} />
        <Route path="/page4" exact={true} component={Page4} />
        <Route path="/complete" exact={true} component={Complete} />
        <Route path="/compatibility" exact={true} component={Compatibility} />
        <Route path="/Result" exact={true} component={Result} />
      </Router>
    </div>
  );
};

export default App;
