import React, { useEffect } from "react";
import { message } from "antd";
import KakaoImage from "assets/images/kakao.png";
import FacebookImage from "assets/images/facebook.png";
import TwitterImage from "assets/images/twitter.png";
import LinkImage from "assets/images/link.png";

import ReactGA from "react-ga";

const success = () => {
  message.success("URL이 복사되었습니다.");
};
export const Share = () => {
  const shareFacebook = () => {
    ReactGA.event({
      category: "SHARE",
      action: "click",
      label: "FACEBOOK",
    });
    window.open(
      `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(
        `https://mbtilovetest.com`
      )}&t=나의 이상형의 MBTI를 찾아보자!!`,
      "_blank",
      "width=600, height: 400"
    );
  };
  const copy = () => {
    ReactGA.event({
      category: "SHARE",
      action: "click",
      label: "LINK",
    });
    success();
    var textarea = document.createElement("textarea");
    textarea.value = `https://mbtilovetest.com`;

    document.body.appendChild(textarea);
    textarea.select();
    textarea.setSelectionRange(0, 9999); // 추가

    document.execCommand("copy");
    document.body.removeChild(textarea);
  };
  const createKakaoButton = () => {
    ReactGA.event({
      category: "SHARE",
      action: "click",
      label: "KAKAO",
    });
    // kakao sdk script이 정상적으로 불러와졌으면 window.Kakao로 접근이 가능합니다
    if (window.Kakao) {
      const kakao = window.Kakao;
      // 중복 initialization 방지
      if (!kakao.isInitialized()) {
        // 두번째 step 에서 가져온 javascript key 를 이용하여 initialize
        kakao.init("070b07fe7cf018e61f15991b3a040fcb");
      }
      kakao.Link.createDefaultButton({
        // Render 부분 id=kakao-link-btn 을 찾아 그부분에 렌더링을 합니다
        container: "#kakao-link-btn",
        objectType: "feed",
        content: {
          title: "당신의 MBTI 이상형 테스트",
          description:
            "나의 MBTI 이상형을 찾아보자! 16가지의 무료성격 유형검사를 해보고 이상형의 MBTI와 당신의 MBTI의 유형을 분석해 드립니다.",
          imageUrl: "https://mbti.inforever.xyz/logo.png",
          link: {
            mobileWebUrl: "https://mbtilovetest.com",
            webUrl: "https://mbtilovetest.com",
          },
        },
        buttons: [
          {
            title: "플레이 하기",
            link: {
              mobileWebUrl: "https://mbtilovetest.com",
              webUrl: "https://mbtilovetest.com",
            },
          },
        ],
      });
    }
  };
  useEffect(() => {
    createKakaoButton();
  }, []);
  return (
    <>
      <div className="flex justify-center px-4 pt-4 pb-4">
        <div
          className="twitter-share-button inline"
          style={{ cursor: "pointer" }}
          onClick={() => {
            ReactGA.event({
              category: "SHARE",
              action: "click",
              label: "TWITTER",
            });
          }}
        >
          <a
            href={`https://twitter.com/intent/tweet?text=MBTI 이상형 찾아보기?&url=https://mbtilovetest.com&hashtags=#MBTI#이상형}`}
            target="_blank"
            rel="noreferrer"
          >
            <img
              src={TwitterImage}
              alt="twitter-share-icon"
              style={{ width: 90 }}
            />
          </a>
        </div>
        <div
          className="facebook-share-button pt-1 inline"
          onClick={() => shareFacebook()}
          style={{ cursor: "pointer" }}
        >
          <img
            src={FacebookImage}
            alt="facebook-share-icon"
            style={{ width: 80 }}
          />
        </div>
        <div
          className="kakao-share-button inline"
          style={{ cursor: "pointer" }}
        >
          <button id="kakao-link-btn">
            <img
              src={KakaoImage}
              alt="kakao-share-icon"
              style={{ width: 90 }}
            />
          </button>
        </div>

        <div
          className="link-share-button inline pt-2"
          onClick={() => copy()}
          style={{ cursor: "pointer" }}
        >
          <img src={LinkImage} alt="link-share-icon" style={{ width: 70 }} />
        </div>
      </div>
    </>
  );
};
